/* eslint-disable no-param-reassign */
import React, { useState } from 'react';
import { Step, Steps, Wizard } from 'react-albus';
import { Button, FormGroup, Row } from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';

import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import AppError from '../../../../helpers/AppError';

import IntlMessages from '../../../../helpers/IntlMessages';
import { getHackathonInfo } from '../../../../helpers/Utils';
import { adminRoot } from '../../../../constants/defaultValues';

import TopNavigation from '../../../../components/wizard/TopNavigation';
import BottomNavigation from '../../../../components/wizard/BottomNavigation';

const StepOne = React.lazy(() =>
  import(/* webpackChunkName: "application-step-one" */ './components/step-one')
);
const StepTwo = React.lazy(() =>
  import(/* webpackChunkName: "application-step-one" */ './components/step-two')
);
const FinalStep = React.lazy(() =>
  import(
    /* webpackChunkName: "application-step-one" */ './components/final-step'
  )
);

const Application = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');

  const {
    application = {
      about: [],
      education: [],
      experience: [],
      online_profiles: [],
      contact: [],
    },
  } = getHackathonInfo(id);

  const [formValues, setFormValues] = useState({
    id,
    application,
  });

  const topNavClick = (stepItem, push) => {
    push(stepItem.id);
  };

  const onClickNext = (goToNext, steps, step) => {
    step.isDone = true;
    if (steps.length - 1 <= steps.indexOf(step)) {
      return;
    }
    goToNext();
  };

  const onClickPrev = (goToPrev, steps, step) => {
    if (steps.indexOf(step) <= 0) {
      return;
    }
    goToPrev();
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.application" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="8" className="mb-5 wizard wizard-default">
          <Wizard>
            <TopNavigation
              className="justify-content-between"
              disableNav={false}
              topNavClick={topNavClick}
            />
            <Steps>
              <Step id="step1">
                <StepOne
                  formValues={formValues}
                  setFormValues={setFormValues}
                />
              </Step>

              <Step id="step2">
                <StepTwo
                  formValues={formValues}
                  setFormValues={setFormValues}
                />
              </Step>

              <Step id="final-step">
                <FinalStep
                  formValues={formValues}
                  setFormValues={setFormValues}
                />
              </Step>
            </Steps>

            <BottomNavigation
              onClickNext={onClickNext}
              onClickPrev={onClickPrev}
              className="justify-content-center "
              prevLabel={<i className="simple-icon-arrow-left" />}
              nextLabel={<i className="simple-icon-arrow-right" />}
            />
          </Wizard>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/basics/${id}`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/links/${id}`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Application;
